import { login } from '@/common/js/login.js';
import { sendAjax } from '@/common/js/sendAjax.js';
import { htmlToText } from '@/common/js/tools.js';
import config from '@/apiConfig';
const { dictDetailUrl,getNewsUrl,getBannerListUrl,cleanRecommendListUrl} = config.api;
export default {
	data() {
		return {
			ylxxList: [],
			bannerList: [],
			cleanRecommendList:[],
			msgList: [],
			modalName: ''
		};
	},
	onLoad: function() {
		//判断是否第一次进入小程序		
		if(uni.getStorageSync('userInfo')){
			this.modalName =null
			login({});
			this.getDict()
		}else{
			this.modalName = 'toUserLogin'
		}
	},
	methods: {
		//获取用户信息
		wxGetUserInfo(res) {
			var that = this;
			if (!res.detail.iv) {
				uni.showToast({
					title: '您取消了授权,登录失败',
					icon: 'none'
				});
				return false;
			}
			//授权成功回调
			let callback = {};
			callback.success = function() {
				that.modalName = null;
				that.getDict();
			};
			login(callback);
		},
		// 获取字典
		getDict() {
			var that = this;
			//娱乐休闲
			let infoOpt = {
				url: dictDetailUrl,
				type: 'POST',
				data: {
					type: 'YLXX',
					pageNum: 1,
					pageSize: 999
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				that.ylxxList = res.list;
				that.getNews();
				that.getBanner();
				that.getCleanRecommend()
			};
			infoCb.error = function(res) {
				if(res.status ==='000'){
					that.modalName = 'toUserLogin'
				}else{
					uni.showModal({
						title: '提示',
						content:'处理失败',
						showCancel: false
					});
				}
			};
			sendAjax(infoOpt, infoCb);
		},
		// 获取公告
		getNews() {
			var that = this;
			let infoOpt = {
				url: getNewsUrl,
				type: 'POST',
				data: {
					pageNum:1,
					pageSize:5
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				//富文本转换
				res.list.forEach(res=>{
					res.title = htmlToText(res.title)
				})
				that.msgList = res.list
			};
			sendAjax(infoOpt, infoCb);
		},
		//获取轮播图
		getBanner(){
			var that = this;
			let infoOpt = {
				url: getBannerListUrl,
				type: 'POST',
				data: {
					pageNum:1,
					pageSize:99
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				that.bannerList = res.list
			};
			sendAjax(infoOpt, infoCb);
		},
		//获取保洁推荐
		getCleanRecommend(){
			var that = this;
			let infoOpt = {
				url: cleanRecommendListUrl,
				type: 'POST',
				data: {
					pageNum:1,
					pageSize:99
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				that.cleanRecommendList = res.list
			};
			sendAjax(infoOpt, infoCb);
		},
		wxGetPhoneNumber(res) {
			console.log(res);
		},
		toCleanNormal(e) {
			uni.navigateTo({
				url: `../cleanNormal/cleanNormal?title=${e.title}`
			});
		},
		gotoDetail(e) {
			if(e==1){
				uni.navigateTo({
					url: './lookImg'
				});
			}else{
				uni.showToast({
					title: '该功能暂未开放！',
					icon: 'none',
					duration: 1000
				});
			}
			
		}
	}
};
